@import 'variables';
@import 'mixins';
@import 'reset';

body {
	font-size: 100%;
	color: #333333;
    font-family: $oswald;
	font-weight: 400;
	&.fixed {
		overflow: hidden;
	}
}

@include selection {
    background-color: $blue;
    color: white;
}

header {
	position: relative;
	background-color: white;
	box-shadow: 0 3px 20px rgba(0,0,0,.1);
	z-index: 20;
    .pre-header {
        display: none;
        padding: .45em 0;
		border-bottom: 1px solid #EEEEEE;
        @media (min-width: 992px) {
            display: block;
        }
        .pre-h-row {
            display: flex;
            justify-content: space-between;
            &__col {
                .pre-h-delivery {
                    display: flex;
                    align-items: center;
                    color: $blue;
                    span {
                        display: block;
                        font-size: .688em;
                        text-transform: uppercase;
                        a {
                            color: $blue;
                            text-decoration: underline;
                            transition: .3s;
                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
                .pre-h-nav {
                    display: flex;
                    li {
                        padding-left: 1.5em;
                        a {
                            display: flex;
                            align-items: center;
                            font-size: .688em;
                            color: $blue;
                            text-transform: uppercase;
                            transition: .3s;
                            span {
                                display: block;
                            }
                            &:hover {
                                color: $red;
                            }
                        }
                    }
                }
            }
        }
    }
    .header-top {
		padding: .75em 0;
		background-color: $blue;
		@media (min-width: 992px) {
			position: relative;
			padding: .875em 0 0;
			background-color: white;
			transition: .3s;
			z-index: 1;
		}
        .head-top-row {
			display: flex;
			justify-content: space-between;
            &__col {
                &--logo {
					@media (min-width: 992px) {
						width: 20%;
					}
                    .logo-header {
						display: inline-block;
						width: 130px;
						@media (min-width: 992px) {
							width: auto;
						}
                        img {
							max-width: 100%;
						}
						&__lg {
							display: none;
							@media (min-width: 992px) {
								display: block;
							}
						}
						&__xs {
							display: block;
							@media (min-width: 992px) {
								display: none;
							}
						}
                    }
                }
                &--search {
					display: none;
					align-items: center;
					width: 50%;
					padding: 0;
					@media (min-width: 992px) {
						display: flex;
					}
					form {
						width: 100%;
					}
					.head-search {
						input {
							width: 100%;
							display: block;
							height: 40px;
							padding-left: 4.5em;
							background-color: $gray-bg;
							border: none;
							border-radius: 50px;
							font-size: .75em;
							text-transform: uppercase;
							transition: .3s;
							@include placeholder {
								color: #999999;
							}
							&:focus {
								background-color: white;
								box-shadow: 0 3px 15px rgba(58,82,163,.25);
							}
						}
					}
                }
                &--cart {
					display: flex;
					justify-content: flex-end;
					align-items: center;
					@media (min-width: 992px) {
						width: 30%;
					}
					.head-nav-cart {
						display: flex;
						align-items: center;
						&__item {
							&--login, &--register {
								display: flex;
								align-items: center;
								padding: 0 15px;
								a {
									display: flex;
									align-items: center;
									color: $blue;
									font-weight: 500;
									text-transform: uppercase;
									transition: .3s;
									span {
										display: none;
										font-size: .75em;
										@media (min-width: 992px) {
											display: block;
										}
									}
									&:hover {
										color: $red;
									}
								}
							}
							&--login {
								border-right: 1px solid #EEEEEE;
							}
							.btn-cart {
								display: flex;
								justify-content: center;
								align-items: center;
								color: white;
								transition: .3s;
								@media (min-width: 992px) {
									width: 60px;
									height: 30px;
									background-color: $red;
									border-radius: 50px;
								}
								span {
									display: block;
									font-size: .75em;
								}
								&:hover {
									box-shadow: 0 3px 10px rgba(255,70,75,.5);
								}
							}
							.btn-menu-xs {
								position: relative;
								width: 25px;
								height: 22px;
								margin-left: 1.25em;
								@media (min-width: 992px) {
									display: none;
								}
								span {
									position: absolute;
									width: 100%;
									height: 4px;
									display: block;
									background-color: white;
									border-radius: 50px;
									transition: .3s;
								}
								&__l1 {
									top: 0;
								} 
								&__l2 {
									@include center-y;
								}
								&__l3 {
									bottom: 0;
								}
								&.open {
									.btn-menu-xs {
										&__l1 {
											@include center-y;
											transform: rotate(45deg);
										}
										&__l2 {
											opacity: 0;
										}
										&__l3 {
											@include center-y;
											transform: rotate(-45deg);
										}
									}
								}
							}
						}
					}
				}
            }
        }
	}
	.header-nav {
		position: fixed;
		width: 100%;
		height: 100%;
		padding: 0 0 5em;
		background-color: white;
		left: -100%;
		opacity: 0;
		z-index: 20;
		overflow: auto;
		transition: .3s;
		@media (min-width: 992px) {
			position: relative;
			width: auto;
			height: auto;
			padding: 0;
			left: 0;
			opacity: 1;
			overflow: initial;
			z-index: auto;
		}
		.container {
			padding: 0;
			@media (min-width: 992px) {
				padding: 0 15px;
			}
		}
		&.open {
			left: 0;
			opacity: 1;
		}
		.head-menu-nav {
			position: relative;
			display: flex;
			flex-direction: column;
			padding: 1em 0;
			@media (min-width: 992px) {
				flex-direction: row;
				justify-content: space-between;
				padding: 0;
			}
			&__item {
				position: relative;
				width: 100%;
				@media (min-width: 992px) {
					position: static;
					width: auto;
					display: block;
				}
				.btn-nav-first {
					position: relative;
					display: block;
					padding: .75em 15px;
					font-size: .75em;
					color: $gray-txt;
					font-weight: 500;
					text-transform: uppercase;
					transition: .3s;
					@media (min-width: 992px) {
						height: 100%;
						display: flex;
						align-items: center;
						justify-content: center;
						padding: 1em 0 1.125em;
						text-align: center;
						line-height: 1.3em;
					}
					&:after {
						content: "";
						position: absolute;
						width: 7px;
						height: 4px;
						background-image: url('../images/caret-head-nav.svg');
						background-size: cover;
						background-position: center;
						background-repeat: no-repeat;
						display: none;
						right: -10px;
						@include center-y;
						@media (min-width: 992px)  {
							display: block;
						}
					}
					span {
						display: block;
					}
					&:hover {
						color: $blue;
					}
				}
				.caret-nav-xs {
					position: absolute;
					width: 70px;
					height: 30px;
					display: flex;
					justify-content: flex-end;
					align-items: center;
					top: 2px;
					right: 0;
					@media (min-width: 992px) {
						display: none;
					}
					img {
						display: block;
						margin-right: 20px;
						transition: .3s;
					}
				}
				&.open-sub1 {
					.caret-nav-xs {
						img {
							transform: rotate(-180deg);
						}
					}
				}
				.sub-menu {
					display: none;
					background-color: $gray-bg;
					padding: .5em 0;
					@media (min-width: 992px) {
						display: block;
						background-color: white;
						padding: 0;
						opacity: 0;
						visibility: hidden;
						transition: .3s;
					}
					&__nav {
						@media (min-width: 992px) {
							position: absolute;
							display: flex;
							width: 100%;
							padding: .5em 1.5em 2em;
							background-color: white;
							left: 0;
							z-index: 2;
						}
						.sub-menu-box {
							position: relative;
							@media (min-width: 992px) {
								width: 25%;
							}
							.btn-nav-second {
								display: block;
								padding: .5em 15px;
								font-size: .75em;
								color: $blue;
								text-transform: uppercase;
								font-weight: 500;
								transition: .3s;
								@media (min-width: 992px) {
									display: inline-block;
									padding: .5em 0;
								}
								&:hover {
									text-decoration: underline;
								}
							}
							.caret-sub-nav-xs {
								position: absolute;
								width: 70px;
								height: 30px;
								display: flex;
								justify-content: flex-end;
								align-items: center;
								top: 0;
								right: 0;
								@media (min-width: 992px) {
									display: none;
								}
								img {
									display: block;
									margin-right: 25px;
									transition: .3s;
								}
							}
							&.open-sub2 {
								.caret-sub-nav-xs {
									img {
										transform: rotate(-180deg);
									}
								}
							}
							&__list {
								display: none;
								padding: 0 1.5em .5em;
								@media (min-width: 992px) {
									display: block;
									padding: 0;
								}
								li {
									a {
										display: inline-block;
										font-size: .75em;
										color: #999999;
										text-transform: uppercase;
										transition: .3s;
										&:hover {
											color: $blue;
											text-decoration: underline;
										}
									}
								}
							}
						}
					}
					&__bg {
						position: fixed;
						display: none;
						width: 100%;
						height: 100%;
						background-color: rgba(0,0,0,.75);
						left: 0;
						z-index: 1;
						@media (min-width: 992px) {
							display: block;
						}
					}
				}
				&.show {
					.btn-nav-first {
						@media (min-width: 992px) {
							color: $blue;
						}
					}
					.sub-menu {
						opacity: 1;
						visibility: visible;
					}
				}
			}
		}
	}
	&.fixed {
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 20;
		animation: headSlide .5s forwards;
		-webkit-animation: headSlide .5s forwards;
		@keyframes headSlide {
			0% {
				top: -170px;
			}
			100% {
				top: 0;
			}
		}
	}
}

.newsletter {
	padding: 1.5em 0;
	background-color: #F7F7F7;
	@media (max-width: 991px) {
		padding: 1em 0;
	}
	.newsletter-content {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		@media (max-width: 991px) {
			flex-direction: column;
		}
		p {
			width: 100%;
			padding: 0 0 1em;
			font-size: 1em;
			color: $blue;
			text-align: center;
			text-transform: uppercase;
			@media (max-width: 991px) {
				padding: 0 0 .5em;
				text-align: center;
			}
			@media (max-width: 480px) {
				font-size: .813em;
			}
		}
		&__input {
			position: relative;
			display: flex;
			margin-left: 1em;
			@media (max-width: 991px) {
				padding-left: 0;
			}
			input {
				display: block;
				width: 350px;
				height: 40px;
				padding-left: 2.5em;
				padding-right: 0;
				background-color: white;
				border: 1px solid #D8D8D8;
				border-radius: 50px;
				font-size: .75em;
				text-transform: uppercase;
				font-weight: 300;
				@media (max-width: 991px) {
					width: 200px;
				}
				@include placeholder {
					color: #999999;
				}
				&:focus {
					border: 1px solid $red;
					box-shadow: none;
				}
				&.mage-error {
					border-color: $red;
					box-shadow: 0 0 20px rgba(220,0,5,.25);
				}
			}
			div {
				&.mage-error {
					position: absolute;
					margin: 0;
					font-size: .75em;
					color: white;
					bottom: -18px;
					left: 0;
				}
			}
			button {
				display: block;
				width: 100px;
				height: 38px;
				padding: 0 1em;
				margin-left: .5em;
				background-color: $red;
				border: 1px solid $red;
				border-radius: 50px;
				font-size: 1em;
				color: white;
				text-transform: uppercase;
				transition: .3s;
				&:hover {
					box-shadow: 0 0 15px rgba(255,70,75,.75);
				}
			}
		}
	}
}
footer {
	position: relative;
	padding: 0;
	background-color: $blue;
	overflow: hidden;
	.row-foot {
		position: relative;
		display: flex;
		flex-wrap: wrap;
		padding: 2.5em 0 0;
		margin: 0 -15px;
		z-index: 1;
		@media (max-width: 991px) {
			padding: 1em 0 0;
		}
		&__col {
			width: 20%;
			padding: 0 15px;
			@media (max-width: 991px) {
				width: 100%;
				padding: .75em 15px;
			}
			&--logo {
				@media (max-width: 991px) {
					width: 100%;
				}
			}
			.foot-logo {
				display: block;
				img {
					width: 100%;
					@media (max-width: 991px) {
						display: block;
						width: auto;
						max-width: 100%;
						margin: 0 auto;
					}
				}
			}
			h3 {
				padding: 0 0 .875em;
				font-size: .875em;
				color: white;
				text-transform: uppercase;
				font-weight: 600;
				@media (max-width: 991px) {
					padding: 0 0 .5em;
				}
			}
			.nav-foot {
				@media (max-width: 991px) {
					display: flex;
					flex-wrap: wrap;
				}
				li {
					position: relative;
					@media (max-width: 991px) {
						width: 50%;
						padding-left: .5em;
						padding-right: .75em;
					}
					&:after {
						content: "";
						position: absolute;
						display: none;
						width: 4px;
						height: 4px;
						background-color: $red;
						border-radius: 100%;
						top: 9px;
						left: 0;
						@media (max-width: 991px) {
							display: block;
						}
					}
					a {
						display: inline-block;
						font-size: .875em;
						color: white;
						opacity: .5;
						transition: .3s;
						@media (max-width: 991px) {
							opacity: 1;
						}
						&:hover {
							opacity: 1;
							text-decoration: underline;
						}
					}
				}
			}
			.foot-contact {
				padding: 1.5em 0 0;
				@media (max-width: 991px) {
					text-align: center;
				}
				&__info {
					li {
						color: white;
						a {
							display: inline-block;
							font-size: .813em;
							color: white;
							opacity: .5;
							transition: .3s;
							&:hover {
								opacity: 1;
								text-decoration: underline;
							}
						}
					}
				}
			}
			.foot-payment {
				display: block;
				margin: 1.5em 0 0;
				@media (max-width: 991px) {
					margin: 1.5em auto 0;
				}
			}
		}
	}
	.foot-copyright {
		padding: 1em 0;
		margin: 3em 0 0;
		font-size: .75em;
		color: white;
		text-align: center;
		font-weight: 300;
		opacity: .5;
		@media (max-width: 991px) {
			margin: 1.5em 0 0;
		}
	}
	.mts-mark {
		position: absolute;
		right: 0;
		bottom: -.35em;
		opacity: .03;
		@media (max-width: 768px) {
			width: 600px;
			left: 0;
			bottom: 0;
			margin: 0 auto;
		}
		@media (max-width: 480px) {
			width: 350px;
		}
	}
}

//MAGENTO EDIT
.loading-mask {
	background: rgba(255,255,255,.5);
	.loader {
		position: absolute;
		width: 45px;
		height: 45px;
		background-image: url(http://10.2.1.190/devmagento/pub/media/wysiwyg/mts/loader-in.svg);
		background-position: center;
		background-size: cover;
		animation: loadAnimateIn 2s linear infinite;
		@include center-all;
		@keyframes loadAnimateIn {
			from {
				transform: rotate(0deg);
			}
			to {
				transform: rotate(360deg);
			}
		}
		img {
			display: none;
		}
	}
}

body {
	&.page-products {
		.columns {
			.column {
				&.main {
					width: auto;
					float: none;
				}
			}
		}
		.category {
			.category-row {
				flex-direction: row-reverse;
			}
		}
	}
	&.nav-open {
		overflow: hidden;
	}
	&.account {
		#maincontent {
			padding: 0 15px;
			margin: 0 auto;
			@media (min-width: 768px) {
				width: 750px;
			}
			@media (min-width: 992px) {
				width: 970px;
			}
			@media (min-width: 1200px) {
				width: 1220px;
			}
		}
		.page {
			&.messages {
				padding: 1em 0 0;
				margin-bottom: 1em;
				.container {
					padding: 0;
				}
			}
		}
		.columns {
			display: flex !important;
			flex-wrap: wrap;
			padding: 1em 0 5em;
			@media (max-width: 991px) {
				padding: 0 0 3em;
			}
			.column {
				width: 70% !important;
				float: none;
				@media (max-width: 768px) {
					order: 1;
				}
				&.main {
					.page-title-wrapper {
						padding: 0 0 1.5em;
						.page-title {
							font-size: 1.5em;
							color: $blue;
							text-transform: uppercase;
							font-weight: 700;
							@media (max-width: 768px) {
								font-size: 1.25em;
							}
						}
						.order-status {
							font-size: .875em;
							font-weight: 600;
						}
						.order-date {
							.label {
								padding: 0 .5em 0 0;
								font-size: .875em;
								color: black;
								line-height: normal;
								font-weight: normal;
							}
							date {
								font-size: .875em;
								font-weight: 600;
							}
						}
						.actions-toolbar {
							justify-content: flex-start;
							margin: 0;
							.actions {
								display: flex;
								.action {
									padding: .35em 1em;
									border: 1px solid $red;
									margin-right: 1em;
									font-size: .875em;
									text-align: center;
									font-weight: 600;
									transition: .3s;
									&.order {
										background-color: $red;
										color: white;
										&:hover {
											background-color: black;
											border-color: black;
										}
									}
									&.print {
										color: $red;
										&:hover {
											background-color: $red;
											color: white;
										}
									}
								}
							}
						}
					}
					.block {
						margin-bottom: 1.5em;
						.block-title {
							display: flex;
							align-items: center;
							margin-bottom: .5em;
							a {
								display: block;
								color: black;
								text-decoration: underline;
								line-height: normal;
								transition: .3s;
								@media (max-width: 768px) {
									font-size: .875em;
								}
								&:hover {
									color: $red;
								}
							}
							strong {
								margin: 0;
								font-size: 1em;
								color: black;
								font-weight: 700;
								@media (max-width: 768px) {
									font-size: .875em;
								}
							}
						}
						.block-content {
							display: flex;
							margin: 0 -5px;
							@media (max-width: 991px) {
