.btn-up {
	position: fixed;
	width: 60px;
	height: 60px;
	background-color: $blue;
	border-radius: 100%;
	bottom: -20%;
	right: 1.5em;
	z-index: 5;
	box-shadow: 8px 8px 20px rgba(0,0,0,0.25);
	transition: .75s;
	cursor: pointer;
	&:after {
		content: "";
		position: absolute;
		width: 20px;
		height: 20px;
		border-top: 4px solid white;
		border-left: 4px solid white;
		transform: rotate(45deg);
		top: 24px;
		@include center-x;
	}
	&:hover {
		background-color: black;
	}
	&:active {
		transform: scale(1.1);
	}
	&.active {
		bottom: 5em;
	}
}

